#exercises {
    background-color: #E8E8E8;
    width: 100%;
    height: auto;
    border-radius: 30px;
    padding: 20px;
    box-sizing: border-box;
    h2 {
        margin: 32px 0px;
        color: #1B1B1B;
        font-size: 32px;
        font-weight: inherit;
        line-height: 1;
        letter-spacing: -0.64px;
    }
}
.filters {
    display: flex;
    flex-direction: row;
    height: auto;
    gap: 6px;
    li {
        border: 1px transparent solid;
        background-color: white;
        width: fit-content;
        padding: 7px 14px;
        font-weight: inherit;
        border-radius: 30px;
        font-size: 14px;
        line-height: 1.3;
        &:hover {
            cursor: pointer;
            border: #7E847F 1px solid;
        }
    }
    svg {
        width: 18px;
        height: 18px;
    }
    input {
        border: transparent;
        background-color: transparent;
        width: 100%;
        height: 100%;
        padding: 12px 14px;
        box-sizing: border-box;
        &::placeholder {
            color: #1B1B1B;
        }
    }
}

.categories {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 20px;
    margin: 40px 0px;
}

.muscle-card {
    border-radius: 12px;
    border: 1px solid rgba(246, 246, 246, 0.30);
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    padding: 96px 0px;
    h3 {
        margin: 0;
        color: #F6F6F6;
        text-align: center;
        font-size: 20px;
        line-height: 1;
    }
    p {
        margin: 0;
        text-align: center;
        color: rgba(246, 246, 246, 0.40);
        font-size: 12px;
        line-height: 1.5;
    }
}

@media screen and (min-width: 768px) {
    .categories {
        margin: 32px 0px;
        gap: 14px;
    }
    .muscle-card {
        padding: 103px 0px;
        width: calc(50% - 7px);
        h3 {
            font-size: 24px;
        }
    }
    #exercises {
        padding: 48px;
        h2 {
            margin: 16px 0px 32px;
            line-height: 1.09;
            letter-spacing: -0.88px;
        }
    }
    .filters {
        gap: 8px;
        li {
            font-size: 16px;
            line-height: 1.5;
        }
    }
}

@media screen and (min-width: 1200px) {
    .categories {
        row-gap: 40px;
        column-gap: 20px;
    }
    .muscle-card {
        width: calc(25% - 15px);
    }
}